import { Component, OnDestroy, OnInit, ViewChild, inject  } from '@angular/core';
import { CommonModule } from '@angular/common';
import { PrimeNgModule } from '../shared/prime-ng/prime-ng.module';

import { itemColumn } from 'src/app/interfaces/tablet.interface';
import { OrdenTrabajo } from '../../interfaces/tarea.interface';
import { TareaService } from '../../services/tarea.service';
import { Subscription } from 'rxjs';
import { Router } from '@angular/router';
import { CustomMessageService } from 'src/app/services/custom-message.service';
import { Table } from 'primeng/table';
import { environment } from 'src/environments/environment';
import { Usuario } from 'src/app/interfaces/login/login-respuesta.interface';
import { UsuarioService } from 'src/app/services/auth/usuario.service';
import { EnumService } from 'src/app/services/enum.service';

@Component({
  selector: 'app-ordenes-trabajo-historico',
  standalone: true,
  imports: [CommonModule, PrimeNgModule],
  templateUrl: './ordenes-trabajo-historico.component.html',
  styleUrls: ['./ordenes-trabajo-historico.component.scss']
})
export class OrdenesTrabajoHistoricoComponent implements OnInit, OnDestroy {
  @ViewChild('dt') dt: Table | undefined;

  url: string = environment.API_URL;

  totalRecords: number = 0;
  mostrarDel: number = 1;
  mostrarAl: number = 10;

  private readonly tareaService: TareaService = inject(TareaService);
  private readonly router: Router = inject(Router);
  private readonly usuarioService = inject(UsuarioService);
  private readonly messageService = inject(CustomMessageService);
  private readonly enumService = inject(EnumService);

  private subs: Subscription[] = [];

  public cols: itemColumn[] = [];
  public selectEstadoOrden: any[] = [];
  public estadoOrdenToSearch: string;
  public ordenesTrabajo: OrdenTrabajo[] = [];

  public loading = true;

  public valorNulleable;
  public valorNulleable2;

  public usuarios: Usuario[] = [];

  public usuario: Usuario;

  ngOnInit() {
    this.cols = [
      { field: 'nombre', header: 'Nombre' },
      { field: 'descripcion', header: 'Descripcion' },
      { field: 'estado', header: 'Estado' },
      // { field: 'usuario', header: 'Asignado' },
    ];

    this.usuario = JSON.parse(localStorage.getItem('usuario'))

    // this.cargarUsuarios();
    this.cargarEstadosOrden()
  }

  applyFilterGlobal($event: any, stringVal: any) {
    this.dt!.filterGlobal(
      ($event.target as HTMLInputElement).value,
      'contains'
    );
  }

  cargarEstadosOrden() {
    this.subs.push(
      this.enumService.getEnum('estado_orden_trabajo').subscribe({
        next: (res) => (this.selectEstadoOrden = res),
        error: (error) => this.messageService.error(error.error.detail),
      })
    );
  }

  // buscarAsignado(valueToSearch: string) {
  //   this.dt!.filterGlobal(
  //     ($event.target as HTMLInputElement).value,
  //     'contains'
  //   );
  // }

  clear(table: Table) {
    table.clear();
    (document.getElementById('buscador') as any).value = '';
    this.valorNulleable = null;
    // (document.getElementById('buscadorAsignado') as any).value = '';
  }

  // cargarUsuarios() {
  //   this.usuarioService.getUsuarios().subscribe({
  //     next: (res) => {
  //       console.log(res);
  //       this.usuarios = res;
  //     },
  //     error: (err) => {
  //       console.error(err);
  //       this.messageService.error(
  //         'Hubo un error al cargar el select de usuarios'
  //       );
  //     },
  //   });
  // }

  loadCustomers(event: any) {
    console.log(event);
    this.loading = true;

    const first = event.first ? event.first : 0;
    const rows = event.rows ? event.rows : 1;
    const page = first / rows + 1;

    var endPoint =
      this.url + `/ordenes_trabajo_paginate?page=${page}&itemsPerPage=${rows}`;

    if (event.globalFilter) {
      endPoint =
        endPoint +
        `&nombre=${event.globalFilter}&descripcion=${event.globalFilter}`;
    }

    // if (
    //   event.filters['usuario'] &&
    //   event.filters['usuario'] != undefined &&
    //   event.filters['usuario'].value != null
    // ) {
    //   endPoint = endPoint + `&usuario=${event.filters['usuario'].value}`;
    // } else if (
    //   event.filters['usuario'] &&
    //   event.filters['usuario'][0] != undefined &&
    //   event.filters['usuario'][0].value != null
    // ) {
    //   endPoint = endPoint + `&usuario=${event.filters['usuario'][0].value}`;
    // }

    endPoint = endPoint + `&usuario=${this.usuario.nombre}`;

    if (
      event.filters['estado'] &&
      event.filters['estado'] != undefined &&
      event.filters['estado'].value != null
    ) {
      endPoint = endPoint + `&estado=${event.filters['estado'].value}`;
    } else if (
      event.filters['estado'] &&
      event.filters['estado'][0] != undefined &&
      event.filters['estado'][0].value != null
    ) {
      endPoint = endPoint + `&estado=${event.filters['estado'][0].value.value}`;
    }

    if (event.sortField) {
      endPoint = endPoint + `&ordenCampo=${event.sortField}`;

      if (event.sortOrder == 1) {
        endPoint = endPoint + '&ordenTipo=ASC';
      }

      if (event.sortOrder == -1) {
        endPoint = endPoint + '&ordenTipo=DESC';
      }
    }
    this.cargarOrdenesTrabajosP(endPoint, first, rows);
  }

  cargarOrdenesTrabajosP(url: string, first: number, rows: number) {
    this.subs.push(
      this.tareaService.getOrdenesTrabajosPaginadas(url).subscribe(
        (response) => {
          console.log(response);
          this.ordenesTrabajo = response.data;
          this.loading = false;
          // this.totalRecords = response.total;
          this.totalRecords = response.totalItems;
          this.loading = false;
          this.mostrarDel = first + 1;
          this.mostrarAl = this.mostrarDel + rows - 1;
          if (this.mostrarAl > this.totalRecords) {
            this.mostrarAl = this.totalRecords;
          }
        },
        (error) => {
          console.error(error);
          this.loading = false;
          this.messageService.error(error.error.detail);
        }
      )
    );
  }

  ngOnDestroy(): void {
    // cerramos todas las subscripciones abiertas
    this.subs.forEach((subscripcion) => {
      subscripcion.unsubscribe();
    });
  }

  navegarAMostrar(id: number): void {
    console.log(id);
    sessionStorage.setItem('lastUrlOrden', '/ordenes-trabajos-historico')
    this.router.navigateByUrl(`ordenes-trabajo/mostrar/${id}`);
  }
}
